import React, { Component } from "react";
import Rating from "@mui/material/Rating";
import { TextField } from "@material-ui/core";

import axios from "axios";

function AddProductModal(props) {
  const [value, setValue] = React.useState(2);
  const [hover, setHover] = React.useState(-1);

  const [productName, setproductName] = React.useState("");
  const [companyName, setcompanyName] = React.useState("");
  const [priceValue, setpriceValue] = React.useState(0);
  const [ratingValue, setratingValue] = React.useState(0);

  if (!props.show) {
    return null;
  }

  function postData(event) {
    console.log("Data posted");

    const productData = {
      productname: productName,
      company: companyName,
      price: priceValue,
      rating: Number(ratingValue),
    };
    if (productData.rating >= 0 && productData.rating <= 5) {
      console.log(productData);

      axios
        .post("http://localhost:5000/products/add", productData)
        .then((res) => console.log(res.data))
        .then((res) => alert(`Producto agregado correctamente`))
        .catch((error) => console.log(error))
        .catch((error) => alert(`Error al cargar el producto`));
    } else {
      alert("El rating debe ser entre 0 y 5");
    }
  }

  return (
    <div className="modal" onClick={props.onClose}>
      <form onSubmit={postData}>
        <div className="modal-container" onClick={(e) => e.stopPropagation()}>
          <div className="modal-header">Nuevo Producto</div>
          <div className="modal-body">
            <TextField
              required
              id="outlined-required"
              label="Nombre"
              fullWidth="true"
              placeholder="Nombre..."
              value={productName}
              onChange={(e) => setproductName(e.target.value)}
            />
          </div>
          <div className="modal-body">
            <TextField
              required
              id="outlined-required"
              label="Empresa"
              fullWidth="true"
              placeholder="Empresa..."
              value={companyName}
              onChange={(e) => setcompanyName(e.target.value)}
            />
          </div>
          <div className="modal-body">
            <TextField
              id="outlined-number"
              label="Precio"
              type="number"
              fullWidth="true"
              value={priceValue}
              onChange={(e) => setpriceValue(e.target.value)}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </div>
          <div className="modal-body">
            <TextField
              id="outlined-number"
              label="Rating (0-5)"
              type="text"
              InputProps={{
                inputProps: {
                  min: 0,
                  max: 5,
                },
              }}
              value={ratingValue}
              onChange={(e) => setratingValue(e.target.value)}
              fullWidth="true"
              InputLabelProps={{
                shrink: true,
              }}
            />
          </div>

          <div className="modal-body">Agregá una foto!</div>

          <form action="/action_page.php">
            <input type="file" id="myFile" name="filename" />
          </form>
          <div className="modal-footer">
            <button className="red-btn" onClick={props.onClose}>
              Close
            </button>
            <button className="blue-btn-footer" type="submit">
              Save{" "}
            </button>
          </div>
        </div>
      </form>
    </div>
  );
}

export default AddProductModal;
